define('wiki-app/router', ['exports', 'ember', 'wiki-app/config/environment'], function (exports, Ember, config) {

  'use strict';

  var Router = Ember['default'].Router.extend({
    location: config['default'].locationType
  });

  Router.map(function () {
    this.resource("article");
  });

  exports['default'] = Router;

});